"use strict"
var moment = require('moment');
moment.locale('zh-cn'); // 

/**
 *  @param {string} time 
 *  @param {bool} friendly fromNow
 */
exports.getLastTimeStr = function(time, friendly) {
    time = moment(time);

    if (friendly) {
        return time.fromNow();
    } else {
        return time.format('YYYY-MM-DD HH:mm');
    }
}

